#herosection {
    height: 100vh;
    position: relative;
    z-index: 1;
    margin-bottom: 200px;
    overflow: hidden;
}

h1, h4 {
    margin: 0;
}

.hero-container {
  position: absolute;
  top: 50%; left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-family: 'Roboto Slab', serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 50%;
  justify-content: space-evenly;
  z-index: 1;
  animation: fadeIn linear 1s;
  -webkit-animation: fadeIn linear 1s;
  -moz-animation: fadeIn linear 1s;
  -o-animation: fadeIn linear 1s;
  -ms-animation: fadeIn linear 1s;
}

.rocket-icon {
  position: absolute;
  height: auto;
  width: 50%;
  bottom: 2%;
  right: -15%;
  pointer-events: none;
  transition: .4s;
}

.rocket-icon.nightmode {
  right: -5%;
  bottom: -2%;
  width: 35%;
  margin: 0;
}

.code-icon {
  position: absolute;
  height: auto;
  width: 50%;
  top: 0%;
  left: -15%;
  -webkit-transform: rotate(-30deg);
          transform: rotate(-30deg);
  margin: 0;
  pointer-events: none;
  transition: .4s;
}

.code-icon.nightmode {
  top: -3%;
  left: -5%;
  -webkit-transform: none;
          transform: none;  
  width: 35%;
}

.gamecontroller-icon {
  position: absolute;
  height: auto;
  width: 50%;
  bottom: 2%;
  left: -15%;
  -webkit-transform: rotate(-30deg);
          transform: rotate(-30deg);
  pointer-events: none;
  transition: .4s;
}

.gamecontroller-icon.nightmode {
  width: 10%;
  bottom: 15%;
  left: 20%;
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
}

.pizza-icon {
  position: absolute;
  height: auto;
  width: 50%;
  top: 0%;
  right: -15%;
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
  pointer-events: none;
  transition: .4s;
}

.pizza-icon.nightmode {
  top: 10%;
  right: 25%;
  width: 5%;
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
}

.hero-title {
    color: #00264d;
    text-align: center;
    margin: 0;
    font-size: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    letter-spacing: 1px;
    pointer-events: none;
    transition: .4s;
}

.hero-title.nightmode {
  color: #f8f8ff;
}

.hero-hi {
