.switch {
    position: absolute;
    bottom: 0;
    margin: 0 5% 2%;
    width: 60px;
    height: 34px;
    z-index: 2;
    transition: .4s;
}

.switch.nightmode {
    margin: 0 2% 2%;
}
  
.switch input {
opacity: 0;
width: 0;
height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #4d4d4d;
    transition: .4s;
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 15px;
    gap: 15px;
}

.slider:before {
position: absolute;
content: "";
height: 26px;
width: 26px;
left: 4px;
bottom: 4px;
background-color: white;
transition: .4s;
}

input:checked + .slider {
background-color: #9c8ef2;
}

input:focus + .slider {
box-shadow: 0 0 1px #9c8ef2;
}

input:checked + .slider:before {
-webkit-transform: translateX(26px);
transform: translateX(26px);
}

.slider {
border-radius: 34px;
}

.slider:before {
border-radius: 50%;
}

.slider-icon {
    width: auto;
    height: 15px;
}

.switch-container {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    z-index: 5;
}

@media only screen and (min-width : 992px) {
    .switch {
        bottom: 0;
        left: 0;
        margin: 0 2% 0.5%;
    }
    .switch.nightmode {
        margin: 0 0.5% 0.5%;
    }
}
